.black {
    fill: black;
    -webkit-transition: fill 1.8s ease-in-out;
    -o-transition: fill 1.8s ease-in-out;
    transition: fill 1.8s ease-in-out;
    opacity: 1;
}
.grey {
    fill: grey;
    -webkit-transition: fill 1.8s ease-in-out;
    -o-transition: fill 1.8s ease-in-out;
    transition: fill 1.8s ease-in-out;
    opacity: 1;
}
.white {
    fill: white;
    -webkit-transition: fill 1.8s ease-in-out;
    -o-transition: fill 1.8s ease-in-out;
    transition: fill 1.8s ease-in-out;
    opacity: 1;
}



